<template>
	<div class="myform">
		<default-view title='我的表单' :formData='data'></default-view>
	</div>
</template>

<script>
	import DefaultView from '@/components/content/formDisplayType/DefaultView'
	export default {
		name: 'MyForm',
		computed: {
			data() {
				return this.$store.getters['form/getFormData']
			}
		},
		components: {
			DefaultView
		},
		created() {
			this.$store.dispatch('form/saveMyform')
		}
	}
</script>

